.featured_slider{
  overflow: hidden;
  width: 100%;
  -webkit-filter:    brightness(80%) ;
  filter:    brightness(80%) ;
  flex-shrink: 0;
  transition: 750ms all ease-in-out;
}

.project{
  font-family: 'Rampart One', cursive;
}

.project_slider{
  position: relative;
  width: 55%;
  overflow-y: hidden;
  overflow-x: hidden;
  display: flex;
  margin: auto;
  align-items: center;
}

.projects_details{
  display: none;
  position: absolute;
}

.project_slider:hover .projects_details{
  display: block;
  padding: 50px 50px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-animation: opac 2s  ;
  animation: opac 2s  ;
}

@-webkit-keyframes opac {
  from{
    opacity: 0;
  }

  to  {
    opacity: 1;
  }
}

@keyframes opac {
  from{
    opacity: 0;
  }

  to  {
    opacity: 1;
  }
}

